@import "shared";

$extra-large-layout-breakpoint: 1200px;
$extra-extra-large-layout-breakpoint: 1600px;

#content-container {
	padding-left: 5%;
	padding-right: 5%;
	padding-top: 80px;
	display: flex;
	flex-wrap: wrap;
}

.text-container {
	text-align: center;
}

#top-text-container {
	order: 1;
	width: 60%;
}

#bottom-text-container {
	order: 3;
}

#photo-container {
	float: right;
	width: 40%;
	padding-left: 24px;
	padding-right: 24px;
	padding-bottom: 24px;
	box-sizing: border-box;
	order: 2;
}

#intro-photo {
	max-width: 100%;
	position: relative;
    top: 50%;
    transform: translateY(-50%);
}

@media only screen and (max-width: $mobile-breakpoint) {
	#content-container {
		flex-direction: column;
	}

	#top-text-container {
		width: unset;
	}

	#intro-photo {
		position: unset;
	    top: unset;
	    transform: unset;
	}

	#photo-container {
		order: 2;
		max-width: 500px;
		width: unset;
		margin-left: auto;
		margin-right: auto;
	}
}

@media only screen and (min-width: $extra-large-layout-breakpoint) {
	#top-text-container {
		width: unset;
	}

	#content-container {
		display: block;
	}

	#intro-photo {
		position: unset;
    top: unset;
    transform: unset;
	}
}

@media only screen and (min-width: $extra-extra-large-layout-breakpoint) {
	#photo-container {
		width: 576px;
	}
}

.about-text {
	margin-bottom: 20px;
	text-align: justify;
}

.by {
	margin-bottom: .3vh;
	margin-top:0;
}

#head {
	margin: 0;
	font-size: 4em;
}

.disclaimer {
	margin-left: 2vw;
	margin-right: 2vw;
	margin-bottom: 3vh;
}

#enter-button {
	width: 50%;
	padding-top:1vh;
	padding-bottom:1vh;
	border: 2px solid black;
	background-color: #2175CF;
	border-radius: 10px;
	margin-left: 25%;
	cursor: pointer;
	margin-bottom: 5vh;
	font-size: 2em;
}

a {
	color: black;
	text-decoration: none;
}

body {
	background-color: #B7A286;
}
